.project__outerdiv {
  margin: 0 10%;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.project__div {
  margin: 25px;
  //   border: 0.5px solid black;
  box-shadow: rgba(0, 0, 0, 0.274) 5px 5px 20px 5px;
  background-color: rgb(236, 232, 232);
  // margin: 10px;
  margin-bottom: 40px;
  font-size: 40px;
  font-family: "Mulish", sans-serif;
  font-family: "Secular One", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
  z-index: 1;
  // height: 80%;
  &--portfolio {
    height: 80%;
  }
  &--fos {
    height: 75%;
  }
  &-- {
    height: 79%;
  }
}

.proImage {
  width: 100%;
  height: 250px;
  border-radius: 20px 20px 0px 0px;
}
.proImage__div {
  width: 100%;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 20px 20px 0px 0px;
  &--portfolio {
    background-image: url("../../Photos/portfolio.PNG");
  }
  &--weathernow {
    background-image: url("../../Photos/weathernow.PNG");
  }
  &--covid {
    background-image: url("../../Photos/covid.jpg");
  }
  &--fos {
    background-image: url("../../Photos/foodorderingsystem.PNG");
  }
  &--ogs {
    background-image: url("../../Photos/groceryshop.png");
  }
  &:hover {
    // filter: brightness(10%);
    // background-image: none;
    background-color: gray;
    background: rgb(53, 52, 52);
    // opacity: 0.1;
  }
  &--link__div {
    filter: brightness(100%);
  }
}

.project__title {
  font-size: 20px;
  margin: 0 10px;
  margin-top: 8px;
  padding: 5px;
  text-align: center;
}
.project__type {
  font-style: italic;
  color: rgb(37, 29, 29);
  font-size: 15px;
  margin: 10 10px;
  font-weight: 200px;
  margin-bottom: 2px;
  margin-top: 30px;
}
.projectdesc__div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30 10px;
  margin-bottom: 30px;
}
.project__desc {
  // width: 80%;
  font-size: 18px;
  font-family: "Crimson Text", serif;
  margin-bottom: 40px;
  font-weight: lighter;
  padding: 0 12px;
  text-align: justify;
  margin: 0 5px;
  // background-color: yellow;
  // margin-left: 25px;
  // margin-right: 25px;
  // margin: 0 25px;
}
.project__language__div {
  //   background-color: RED;
  // display: grid;
  // grid-template-columns: 0.8fr 0.8fr 0.3fr;
  display: flex;
  flex-direction: row;
  // margin-top: 10px;
  margin-bottom: 10px;

  &--java {
    margin-top: 10px;
    display: flex;
  }
}
.imageLink__div {
  background-color: red;
  z-index: 1;
}
.project__language {
  text-align: center;
  font-family: "Roboto Mono", monospace;
  margin: 3px;
  font-size: 15px;
  padding: 3px;
  // font-family: "Quicksand", sans-serif;
  // font-weight: bold;
  font-weight: 400;
  // border: 2px solid black;
  // border-radius: 10px;
}
.link__div {
  margin: 2px;
  // opacity: 0;
  display: none;
  // &:hover {
  //   z-index: 1;
  //   display: block;
  // }
}
.proImage__div:hover .link__div {
  display: block;
  filter: brightness(100%);
}
.link__div {
  filter: brightness(100%);
}
.link {
  color: white;
  font-size: 18px;
}
@media (max-width: 500px) {
  .project__outerdiv {
    margin: 0 10%;
    display: block;
  }
}
